<template>
  <div class="app-container">
    <!---->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formInline.group" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search(formInline)"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add"></el-button>
      </el-form-item>
    </el-form>

    <el-table
        :data="tables"
        border
        style="width: 100%">

      <el-table-column
          prop="id"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="aid"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="aname"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="agroup"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="cause"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="lid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="status"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="applicationTime"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          fixed="right"
          label="">
        <template slot-scope="scope">
          <div v-if="scope.row.status == '' ">
            <el-button @click="agree(scope.$index)" type="text" size="small"></el-button>
            <el-button @click="notagree(scope.$index)" type="text" size="small"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
export default {
  methods: {
    agree(index) {
      this.tableData[index].status="";
      let aid =this.tableData[index].aid
      let assetData =  JSON.parse(localStorage.getItem('assetData'))
      assetData.forEach(function(value,index){
        if(value.id ==  aid){
          value.status = 1
        }
      })
      localStorage.setItem('assetData', JSON.stringify(assetData))
    },
    notagree(index) {
      this.tableData[index].status="";

    },

  },
  computed: {
    // 
    tables () {
      const {
        name,
        group
      } = this.formInline
      return this.tableData.filter((item) => { //
        let matchname=true;
        let matchgroup=true;
        if(name){
          // debugger
          const keys=name.toUpperCase().replace("","").split("");
          matchname=keys.every(key=>item.name.toUpperCase().includes(key));
        }
        if(group){
          // debugger
          const keys=group.toUpperCase().replace("","").split("");
          // console.log(keys)
          matchgroup=keys.every(key=>item.group.toUpperCase().includes(key));
          // console.log(matchname)
        }
        return matchname && matchgroup
      });
    }
  },

  data() {
    return {

      formInline: {
        name: '',
        group: ''
      },

      tableData: JSON.parse(localStorage.getItem('lendDatas'))
    }
  }
}
</script>