<template>
  <div class="app-container">
    <!---->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formInline.group" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <el-table
        :data="tables"
        border
        style="width: 100%">

      <el-table-column
          prop="id"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="aid"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="aname"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="agroup"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="cause"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="lid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="status"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="returnTime"
          label="">
      </el-table-column>


    </el-table>

  </div>
</template>

<script>
export default {
  methods: {
  },
  created() {

  },
  computed: {
    // 
    tables () {
      const {
        name,
        group
      } = this.formInline
      return this.tableData.filter((item) => { //
        let matchname=true;
        let matchgroup=true;
        if(name){
          // debugger
          const keys=name.toUpperCase().replace("","").split("");
          matchname=keys.every(key=>item.name.toUpperCase().includes(key));
        }
        if(group){
          // debugger
          const keys=group.toUpperCase().replace("","").split("");
          // console.log(keys)
          matchgroup=keys.every(key=>item.group.toUpperCase().includes(key));
          // console.log(matchname)
        }
        return matchname && matchgroup
      });
    }
  },
  data() {
    return {
      formInline: {
        name: '',
        group: ''
      },
      tableData:  JSON.parse(localStorage.getItem('returnDatas'))
    }
  }
}
</script>