<template>
  <div class="app-container">
    <div style="margin: 0 0 20px 0"></div>
    <!---->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input placeholder="" v-model="scrapName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="dialogVisible = true"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button @click="applyHistory"></el-button>
      </el-form-item>
      `
    </el-form>

    <!--    -->
    <div class="scarp-history">
      
      <template v-for="item in scrapData">
        <el-descriptions :title="item.s_name" class="history-item">
          <el-descriptions-item label="">{{ item.s_id }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.s_uid }}</el-descriptions-item>
          <el-descriptions-item label="">
            <el-tag size="small" :type="item.s_tag===''?'danger':''">{{ item.s_tag }}</el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="">{{ item.s_create }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.s_time }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.s_res }}</el-descriptions-item>
        </el-descriptions>
      </template>
    </div>

    <!--    -->
    <el-dialog title="" :visible.sync="dialogVisible"
               width="30%" :before-close="handleClose">
      <!--      -->
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="id" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="d_name" :disabled="true" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="uid" :disabled="true" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="creatTime" :disabled="true" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="desTime" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="tag">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="result"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit" :plain="true"> </el-button>
      </span>
    </el-dialog>

    <!--    -->
    <el-dialog title="" :visible.sync="historyVisible"
               width="50%" :before-close="handleClose">

      <el-table stripe :data="scrapHistory" style="width: 100%">
        <el-table-column prop="s_id" label=""/>
        <el-table-column prop="s_name" label=""/>
        <el-table-column prop="s_time" label=""/>
        <el-table-column prop="s_uid" label=""/>
        <el-table-column prop="s_res" label=""/>
        <el-table-column prop="s_status" label="" width="200">
          <template slot-scope="scope">
            <div>
              <template v-if="scope.row.s_status===''|| scope.row.s_status===''">
                <el-button type="text" size="small" @click="toggle(scope.row)">{{ scope.row.s_status }}</el-button>
              </template>
              <template v-else>
                <el-collapse accordion>
                  <el-collapse-item>
                    <template slot="title">
                      <span style="color: #f56c6c;background: transparent">
                        {{ scope.row.s_status }}
                        <i class="header-icon el-icon-info"/>
                      </span>
                    </template>
                    <div>{{ scope.row.s_detail }}</div>
                  </el-collapse-item>
                </el-collapse>
                <!--                <el-collapse>-->
                <!--                  <el-collapse-item :title="scope.row.s_status" style="color: red;">-->
                <!--                    <div> {{ scope.row.s_detail }}</div>-->
                <!--                  </el-collapse-item>-->
                <!--                </el-collapse>-->

              </template>

            </div>
          </template>
        </el-table-column>


      </el-table>

    </el-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  name: "Scrap",
  data() {
    return {
      source: JSON.parse(localStorage.getItem('scrapData'))
          .sort((a, b) => dayjs(b.s_time) - dayjs(a.s_time)),
      assetData: JSON.parse(localStorage.getItem('assetData')),
      scrapHistory: JSON.parse(localStorage.getItem('scrapHistory')) || [],
      scrapData: '',  // 
      scrapName: '',  // 
      dialogVisible: false, // 
      id: '',  // id
      d_name: '',
      uid: JSON.parse(localStorage.getItem('user'))[0].uid,
      creatTime: '',
      desTime: dayjs().format('YYYY-MM-DD'), // 
      tag: '', // 
      result: '', // 
      flag: false, //

      historyVisible: false, // 
    }
  },
  created() {
    this.scrapData = this.source
  },
  methods: {
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
    },
    //
    onSubmit() {
      //scrapData
      if (this.flag) {
        this.dialogVisible = false
        let scrapItem = {
          s_id: this.id,
          s_name: this.d_name,
          s_create: this.creatTime,
          s_time: this.desTime,
          s_uid: this.uid,
          s_res: this.result,
          s_tag: this.tag,
          s_status: '',
          s_detail: '', // 
        }
        this.scrapHistory.push(scrapItem)
        localStorage.setItem('scrapHistory', JSON.stringify(this.scrapHistory))
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });

        //   this.source.push(scrapItem)
        //   this.source.sort((a, b) => dayjs(b.s_time) - dayjs(a.s_time))
        //   localStorage.setItem('scrapData', JSON.stringify(this.source))
        //   //  assetData
        //   let newAssetData = this.assetData.filter(item => item.id != this.id)
        //   localStorage.setItem('assetData', JSON.stringify(newAssetData))
      } else {
        this.$notify.error({
          title: '',
          message: ''
        });
        // this.$message.error('');
      }
    },
    //
    applyHistory() {
      this.historyVisible = true
      console.log(this.scrapHistory);
    },

  },
  watch: {
    // 
    scrapName(val) {
      if (!val) {
        this.scrapData = this.source
      } else {
        this.scrapData = this.source.filter(item =>
            item.s_name.indexOf(val) >= 0
        )
      }
    },
    //  id
    id(val) {
      let data = this.assetData.filter(item => item.id === val)[0]
      if (data) {
        this.flag = true
        this.d_name = data.name
        this.creatTime = data.storageTime
        this.desTime = dayjs(this.desTime).format('YYYY-MM-DD')
      } else {
        this.flag = false
        this.$message.error('');
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.app-container {
  justify-content: center;
  //border: 1px solid red;
  position: relative;
  padding: 10px 0;

  .scarp-history {
    max-height: 50vh;
    position: absolute;
    min-width: 75%;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    margin-top: 10px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);

    .history-item {
      margin: 0 0 10px 0;
      padding-bottom: 10px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
}

</style>