<template>
  <div class="app-container">
    <div style="margin: 0 0 20px 0"></div>
    <!--    -->
    <el-table :data="form" stripe style="width: 80vw">
      <!--      <el-table-column prop="uni_id" label=""/>-->
      <el-table-column prop="m_id" label=""/>
      <el-table-column prop="m_name" label=""/>
      <el-table-column prop="root" label=""/>
      <el-table-column prop="m_create" label=""/>
      <el-table-column prop="m_history" label=""/>
      <el-table-column prop="m_status" label=""/>
      <el-table-column prop="m_desc" label=""/>

      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <template v-if="scope.row.m_status==''">
            <el-button @click="handleAccept(scope.row,'success')" type="text" size="small"></el-button>
            <el-button @click="handleAccept(scope.row,'fail')" type="text" size="small"></el-button>
          </template>
          <template v-else-if="scope.row.m_status==''">
            <el-button @click="handleAccept(scope.row)" type="text" size="small"></el-button>
          </template>
        </template>
      </el-table-column>

    </el-table>
  </div>
</template>

<script>
export default {
  name: "CenterOfAccept",
  data() {
    return {
      maintainData: JSON.parse(localStorage.getItem('maintainData'))
          .filter(item => item.m_status != ''),
      form: [],
    }
  },
  //
  beforeRouteEnter(to, from, next) {
    let user = JSON.parse(localStorage.getItem('user'))[0]
    if (user.identity === '') {
      next()
    } else {
      next('/noAllow')
    }
  },
  created() {
    this.maintainData.map(item => {
      for (let val of item.m_history) {
        this.form.push({
          uni_id: val.id,
          m_id: item.m_id,
          m_name: item.m_name,
          root: val.root,
          m_create: item.m_create,
          m_history: val.time,
          m_status: val.status,
          m_desc: val.desc
        })
      }
    })
  },
  methods: {
    //  ,  /  
    handleAccept(val, flag) {
      // console.log(this.form);
      this.form.map(item => {
        if (item.uni_id === val.uni_id) {
          if (flag === 'success') item.m_status = ''
          else if (flag === 'fail') item.m_status = ''
          else item.m_status = ''
        }
      })
      let maintain = JSON.parse(localStorage.getItem('maintainData'))
      maintain.map(item => {
        let hasMaintain = false
        for (let history of item.m_history) {
          if (history.id === val.uni_id) {
            if (flag === 'success') history.status = ''
            else if (flag === 'fail') history.status = ''
            else history.status = ''
          }
          // eslint-disable-next-line no-unused-vars
          if (history.status === '') hasMaintain = true
          if (!hasMaintain) item.m_status = ''
        }
      })
      localStorage.setItem('maintainData', JSON.stringify(maintain))
    },
  }
}
</script>

<style scoped>

</style>