<template>
  <div class="app-container">
    <!---->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label=":">
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formInline.group" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="add"></el-button>
      </el-form-item>
    </el-form>

    <el-table
        :data="tables"
        border
        style="width: 100%">

      <el-table-column
          prop="id"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="name"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="num"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="group"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="status"
          label=""
          width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.status==1">
            
          </div>
          <div v-if="scope.row.status==2">
            
          </div>
          <div v-if="scope.row.status==3">
            
          </div>
          <div v-if="scope.row.status==0">
            
          </div>
        </template>
      </el-table-column>

      <el-table-column
          prop="storageTime"
          label=""
          width="180">
      </el-table-column>

      <el-table-column
          prop="storage_uid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="out_uid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          fixed="right"
          label="">
        <template slot-scope="scope">
          <el-button @click="edit(scope.$index,scope.row)" type="text" size="small"></el-button>
          <el-button @click="deleterow(scope.$index)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-dialog title=""
               :visible.sync="editFormVisible"
               :close-on-click-modal="false"
               class="form"
               :before-close="handleClose">
      <el-form :model="form" label-width="80px" :rules="formRules" ref="form">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="num">
          <el-input v-model="form.num" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group prop="group" v-model="form.group">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group prop="group" v-model="form.status">
            <el-radio label="0"></el-radio>
            <el-radio label="1"></el-radio>
            <el-radio label="2"></el-radio>
            <el-radio label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.storageTime" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="handleCancel()"></el-button>
        <el-button type="primary" @click.native="saveOrUpdate()">
          <div v-if=" this.currentType == 'edit' ">
            
          </div>
          <div v-if=" this.currentType == 'add' ">
            
          </div>
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  methods: {
    //dialog
    handleClose() {
      /*done();
      location.reload();*/
      this.editFormVisible = false;
    },

    //
    handleCancel() {
      this.editFormVisible = false;
    },

    //
    saveOrUpdate() {
      if(this.currentType == "add"){
        this.form.storageTime = this.form.storageTime;
        this.form.id = this.currentid+1;
        this.form.storage_uid = this.currentSid;
        this.form.storage_uid =this.uid;
        this.tableData.push(this.form);
        localStorage.setItem('assetData', JSON.stringify(this.tableData))
      };
      if( this.currentType == "edit" ){
        this.record.id=this.tableData[this.currentIndex].id
        this.record.name=this.tableData[this.currentIndex].name
        this.record.uname=this.form.name
        this.record.num=this.tableData[this.currentIndex].num
        this.record.unum=this.form.num
        this.record.status=this.tableData[this.currentIndex].status
        this.record.ustatus=this.form.status
        this.record.group=this.tableData[this.currentIndex].group
        this.record.ugroup=this.form.group
        //


        this.updateRecordData.push(this.record);

        this.tableData[this.currentIndex].name=this.form.name;
        this.tableData[this.currentIndex].num=this.form.num;
        this.tableData[this.currentIndex].status=this.form.status;
        this.tableData[this.currentIndex].id=this.form.id;
        this.tableData[this.currentIndex].storageTime=this.form.storageTime;
        this.tableData[this.currentIndex].group=this.form.group;
        this.tableData[this.currentIndex].storage_uid=this.form.storage_uid;
        this.tableData[this.currentIndex].out_uid=this.form.out_uid;
      };
      //post
      localStorage.setItem('assetData', JSON.stringify(this.tableData))
      localStorage.setItem('updateRecordData', JSON.stringify(this.updateRecordData))
      this.editFormVisible = false;
    },
    add(){
      this.currentType = "add";
      this.editFormVisible = true;
    },
    edit(index, row) {
      this.currentType = "edit";
      this.editFormVisible = true;
      this.form = Object.assign({}, row); //
      this.currentRow = row;
      this.currentIndex = index;


    },
    deleterow(index) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.tableData.splice(index, 1)
        localStorage.setItem('assetData', JSON.stringify(this.tableData))
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },

  },
  computed: {
    // 
    tables () {
      const {
        name,
        group
      } = this.formInline
      return this.tableData.filter((item) => { //
        let matchname=true;
        let matchgroup=true;
        if(name){
          // debugger
          const keys=name.toUpperCase().replace("","").split("");
          matchname=keys.every(key=>item.name.toUpperCase().includes(key));
        }
        if(group){
          // debugger
          const keys=group.toUpperCase().replace("","").split("");
          // console.log(keys)
          matchgroup=keys.every(key=>item.group.toUpperCase().includes(key));
          // console.log(matchname)
        }
        return matchname && matchgroup
      });
    }
  },

  //
  created() {
    let tableData = [{
      id: '1',
      name: 'A',
      num: '10',
      group: '',
      status: '1',
      storageTime:  '2021-05-04',
      storage_uid: '1',
      out_uid: ''
    }, {
      id: '2',
      name: 'B',
      num: '10',
      group: '',
      status: '0',
      storageTime:  '2021-05-04',
      storage_uid: '1',
      out_uid: ''
    }, {
      id: '3',
      name: 'C',
      num: '10',
      group: '',
      status: '0',
      storageTime:  '2021-05-04',
      storage_uid: '1',
      out_uid: ''
    }, {
      id: '4',
      name: 'D',
      num: '10',
      group: '',
      status: '0',
      storageTime:  '2021-05-04',
      storage_uid: '1',
      out_uid: ''
    }]

    localStorage.setItem('assetData', JSON.stringify(tableData))

    this.uid = JSON.parse(localStorage.getItem('user')).uid
  },

  data() {
    return {
      uid:'',
      data:[],
      filterDataShow: [], //
      listSearch: [],
      currentSid: '1',
      currentid: 4,
      currentRow: {},
      currentIndex: '',
      editFormVisible: false,
      currentType: '',
      form: {
        id: '',
        name: '',
        num: '',
        group: '',
        status: '',
        storageTime:  '',
        storage_uid: '',
        out_uid: ''
      },
      record: {
        id:'',
        name: '',
        num: '',
        group: '',
        status: '',
        uname: '',
        unum: '',
        ugroup: '',
        ustatus: ''
      },
      formInline: {
        name: '',
        group: ''
      },
      updateRecordData:  [],
      tableData:  JSON.parse(localStorage.getItem('assetData'))
    }
  }
}
</script>