<template>
  <div class="app-container">
    <div class="chart-container">
      <div id="chart" class="chart" style="height:500px;width:100%"/>
    </div>
  </div>
</template>
<script>
import * as echarts from 'echarts';

export default {
  data() {
    return {
      xData: ['', '', '', '']
    }
  },
  created() {


  },
  mounted() {
    this.setChart()
  },
  methods: {
    setChart() {
      let lendNum = 0
      let xzNum = 0
      let lendsDatas = JSON.parse(localStorage.getItem('lendDatas'))

      for (var i = 0; i < lendsDatas.length; i++) {
        if (lendsDatas[i].status == '') {
          lendNum = lendNum + 1;
        }
      }

      let assetData = JSON.parse(localStorage.getItem('assetData'))
      assetData.forEach(function (value, index) {
        if (value.status == 0) {
          xzNum = xzNum + 1;

        }
      })
      console.log(lendNum)
      // domecharts
      this.chart = echarts.init(document.getElementById('chart'))
      //console.log(this.chart)

      // 
      var option = {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis'
        },

        // x,data
        xAxis: {
          type: 'category',
          data: this.xData
        },
        // y
        yAxis: {
          type: 'value'
        },
        //  type 
        series: [{
          // 
          data: [1, 1, 2, 3],
          // 
          type: 'bar'
        }]
      }

      this.chart.setOption(option)
    }
  }
}
</script>
