<template>
  <div class="app-container">
    <div style="margin: 0 0 20px 0"></div>
    <!---->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input placeholder="" v-model="checkName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select placeholder="" v-model="checkTime">
          <el-option label="" value="week"></el-option>
          <el-option label="" value="month"></el-option>
          <el-option label="" value="year"></el-option>
          <el-option label="" value="all"></el-option>
        </el-select>
      </el-form-item>
      <!--      <el-form-item>-->
      <!--        <el-button type="primary"></el-button>-->
      <!--      </el-form-item>-->
    </el-form>

    <el-table :data="maintainData" border style="width: 100%; max-height:100%;">
      <el-table-column prop="m_id" label=""/>
      <el-table-column prop="m_name" label=""/>
      <el-table-column prop="m_group" label=""/>
      <el-table-column prop="m_create" label=""/>
      <el-table-column prop="m_status" label=""/>
      <el-table-column prop="u_id" label=""/>
      <el-table-column prop="m_ohter" label="">
        <template slot-scope="scope">
          <div>
            <el-button type="text" size="small" @click="toggle(scope.row)"></el-button>
            <el-button type="text" size="small" @click="showHistory(scope.row)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!--    -->
    <el-dialog title="" :visible.sync="dialogVisible"
               width="30%" :before-close="handleClose">
      <!--      -->
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.m_id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.m_name" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.m_group" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.u_id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.m_desc"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </span>
    </el-dialog>

    <!--    -->
    <el-dialog title="" :visible.sync="historyVisible"
               width="50%">
      <template v-if="hasHistory">
        <el-descriptions v-for=" (item,i) in maintainHistory" :key="i"
                         :title="item.m_time"
                         style="border-bottom: 1px solid rgba(0,0,0,.25);
                                padding: 10px 0">
          <el-descriptions-item label="">{{ item.m_id }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.m_name }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.u_id }}</el-descriptions-item>
          <el-descriptions-item label="">{{ item.m_desc }}</el-descriptions-item>
          <el-descriptions-item label="">
            <template v-if="item.status===''">
              <el-tag size="small" type="danger">{{ item.status }}</el-tag>
            </template>
            <template v-else-if="item.status===''">
              <el-tag size="small" type="info">{{ item.status }}</el-tag>
            </template>
            <template v-else>
              <el-tag size="small" :type="item.status===''?'success':''">{{ item.status }}</el-tag>
            </template>
          </el-descriptions-item>
        </el-descriptions>
        <br>
      </template>
      <template v-else>
        
      </template>
    </el-dialog>
  </div>
</template>


<script>
import dayjs from "dayjs"

export default {
  created() {
    this.assetData = JSON.parse(localStorage.getItem('assetData'))
    this.maintainData = JSON.parse(localStorage.getItem('maintainData'))
    // this.maintainHistory = JSON.parse(localStorage.getItem('maintainHistory')) || []
    // console.log(this.applyMaintain)
    if (!this.maintainData) {
      let maintain = []
      this.assetData.map((item) => {
        maintain.push({
          m_id: item.id,
          m_name: item.name,
          m_group: item.group,
          m_create: dayjs(item.storageTime).add(1, 'year').format('YYYY-MM-DD'),
          m_status: '',
          m_uid: '',
          m_history: []
        })
      })
      maintain.sort((a, b) => dayjs(a.m_create) - dayjs(b.m_create))
      localStorage.setItem('maintainData', JSON.stringify(maintain))
    }
  }
  ,
  data() {
    let assetData    // 
    let maintainData // 
    // let applyMaintain
    let checkTime = '' // 
    let checkName = ''    // 
    let maintainHistory // 
    let today = dayjs().format('YYYY-MM-DD')

    return {
      dialogVisible: false,
      historyVisible: false,
      hasHistory: false,
      assetData,
      maintainData,
      checkTime,
      checkName,
      maintainHistory,
      today,
      form: {
        m_id: '',
        m_name: '',
        m_group: '',
        m_time: '',
        m_status: '',
        u_id: '',
        m_desc: ''
      }
    }
  },
  watch: {
    //
    checkTime(value) {
      if (value !== 'all') {
        this.maintainData = JSON.parse(localStorage.getItem('maintainData')).filter(item => dayjs(item.m_create).isSame(this.today, value))
      } else {
        this.maintainData = JSON.parse(localStorage.getItem('maintainData'))
      }
    }
    ,
    //
    checkName(value) {
      if (value) {
        this.maintainData = JSON.parse(localStorage.getItem('maintainData'))
            .filter(item => item.m_name.indexOf(value) >= 0)
      } else {
        this.maintainData = JSON.parse(localStorage.getItem('maintainData'))
      }
    }
  }
  ,
  methods: {
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
    },
    toggle(value) {
      this.dialogVisible = true
      this.form = this.maintainData.filter(item => item.m_id === value.m_id)[0]
      this.form.u_id = JSON.parse(localStorage.getItem('user'))[0].uid
    },
    //
    onSubmit() {
      if (!this.form.m_desc) {
        this.$notify.error({
          title: '',
          message: ''
        });
      } else {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
        this.dialogVisible = false
        this.maintainData.filter(item => {
          if (item.m_id === this.form.m_id) {
            let time = dayjs().format('YYYY-MM-DD')
            item.m_status = ''
            item.m_history.push({
              id: dayjs(), // 
              root: this.form.u_id,
              time: time,
              status: '',
              desc: this.form.m_desc || ''
            })
          }
        })
        localStorage.setItem('maintainData', JSON.stringify(this.maintainData))
      }

    },
    showHistory(value) {
      this.historyVisible = true
      if (value.m_history.length) {
        this.hasHistory = true
      } else {
        this.hasHistory = false
      }
      let his = []
      for (let i in value.m_history) {
        his.push({
          m_id: value.m_id,
          m_name: value.m_name,
          u_id: value.m_history[i].root,
          m_desc: value.m_history[i].desc,
          m_time: value.m_history[i].time,
          status: value.m_history[i].status,
        })
      }
      this.maintainHistory = his
    }
  }
}
</script>

<style scoped>

</style>