<template>
  <div class="app-container">
    <div style="margin: 0 0 20px 0"></div>
    <!--    -->
    <el-table :data="scrapHistory" stripe style="width: 80vw">
      <el-table-column prop="s_id" label="">
      </el-table-column>
      <el-table-column prop="s_name" label="">
      </el-table-column>
      <el-table-column prop="s_uid" label="">
      </el-table-column>
      <el-table-column prop="s_create" label="">
      </el-table-column>
      <el-table-column prop="s_time" label="">
      </el-table-column>
      <el-table-column prop="s_status" label="">
      </el-table-column>
      <el-table-column prop="s_res" label="">
      </el-table-column>

      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope" v-if="scope.row.s_status===''">
          <el-button @click="handlePass(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleBack(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--    -->
    <el-dialog title="" :visible.sync="dialogVisible"
               width="30%" :before-close="handleClose">
      <el-input type="textarea" :rows="4" placeholder="" v-model="textarea">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit" :plain="true"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Audit",
  data() {
    return {
      dialogVisible: false,
      textarea: '',
      row: '', // 
      scrapHistory: JSON.parse(localStorage.getItem('scrapHistory')) || [],
    }
  },
  //
  beforeRouteEnter(to, from, next) {
    let user = JSON.parse(localStorage.getItem('user'))[0]
    if (user.identity === '') {
      next()
    } else {
      next('/noAllow')
    }
  },
  methods: {
    //
    handlePass(row) {
      this.scrapHistory.map(item => {
        if (item.s_id === row.s_id) {
          item.s_status = ''
        }
      })
      //
      localStorage.setItem('scrapHistory', JSON.stringify(this.scrapHistory))
      //
      let scrapData = JSON.parse(localStorage.getItem('scrapData'))
      scrapData.push(row)
      localStorage.setItem('scrapData', JSON.stringify(scrapData))
      //
      let assetData = JSON.parse(localStorage.getItem('assetData')).filter(item => item.id != row.s_id)
      localStorage.setItem('assetData', JSON.stringify(assetData))
    },
    //  
    handleBack(row) {
      this.dialogVisible = true
      this.row = row
      console.log(this.row);
    },
    //
    onSubmit() {
      this.dialogVisible = false
      this.scrapHistory.map(item => {
        if (item.s_id === this.row.s_id) {
          item.s_detail = this.textarea
          item.s_status = ''
        }
      })
      localStorage.setItem('scrapHistory', JSON.stringify(this.scrapHistory))
      console.log(this.scrapHistory);
    },
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
    },
  },
}
</script>

<style scoped>

</style>