<template>
  <div class="app-container">
    <!---->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label=":">
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formInline.group" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

    </el-form>
    <el-table
        :data="tables"
        border
        style="width: 100%">

      <el-table-column
          prop="id"
          label=""
          width="150">
      </el-table-column>

      <el-table-column
          prop="name"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="num"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="group"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="status"
          label=""
          width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.status==1">
            
          </div>
          <div v-if="scope.row.status==2">
            
          </div>
          <div v-if="scope.row.status==3">
            
          </div>
          <div v-if="scope.row.status==0">
            
          </div>
        </template>
      </el-table-column>

      <el-table-column
          prop="storageTime"
          label=""
          width="180">
      </el-table-column>

      <el-table-column
          prop="storage_uid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          prop="out_uid"
          label=""
          width="120">
      </el-table-column>

      <el-table-column
          fixed="right"
          label="">
        <template slot-scope="scope">
          <el-button @click="sendlendApplication(scope.$index,scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
export default {
  methods: {
    // 
    nowDate() {
      let nowDate = new Date()
      let date = nowDate.getFullYear() + '-' + nowDate.getMonth() + '-' + nowDate.getDate()
      return date
    },

    sendlendApplication(index, row) {
      //
      this.lendid++
      this.lendData.id = this.lendid
      this.lendData.aid = row.id
      this.lendData.aname = row.name
      this.lendData.agroup = row.group
      this.lendData.cause = ''
      this.lendData.lid = '1'//JSON.parse(localStorage.getItem('user')).uid
      this.lendData.status = ''
      this.lendData.applicationTime = this.nowDate();

      this.lendDatas.push(this.lendData);

      localStorage.setItem('lendDatas', JSON.stringify(this.lendDatas))

    },


  },
  computed: {
    // 
    tables() {
      const {
        name,
        group
      } = this.formInline
      return this.tableData.filter((item) => { //
        let matchname = true;
        let matchgroup = true;
        if (name) {
          // debugger
          const keys = name.toUpperCase().replace("", "").split("");
          matchname = keys.every(key => item.name.toUpperCase().includes(key));
        }
        if (group) {
          // debugger
          const keys = group.toUpperCase().replace("", "").split("");
          // console.log(keys)
          matchgroup = keys.every(key => item.group.toUpperCase().includes(key));
          // console.log(matchname)
        }
        return matchname && matchgroup
      });
    }
  },

  //
  created() {
    let lendDatas = []
    localStorage.setItem('lendDatas', JSON.stringify(lendDatas))
    let assetData = JSON.parse(localStorage.getItem('assetData'))
    for (var i = 0; i < assetData.length; i++) {
      if (assetData[i].status == '0') {
        this.tableData.push(assetData[i])
      }
    }
    console.log(this.tableData)
  },

  data() {
    return {
      lendid: 0,
      filterDataShow: [], //
      listSearch: [],
      editFormVisible: false,
      currentType: '',
      lendData: {
        id: '',
        aid: '',
        aname: '',
        agroup: '',
        cause: '',
        lid: '',
        status: '',
        applicationTime: '',
      },

      formInline: {
        name: '',
        group: ''
      },
      tableData: [],
      lendDatas: JSON.parse(localStorage.getItem('lendDatas')),
    }
  }
}
</script>